*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
  display: flex;
}

body::-webkit-scrollbar {
  -webkit-appearance: none;
  appearance: none;
  width: 5px;
  background: #000;
}

body::-webkit-scrollbar-thumb {
  background: #fff;
  border-radius: 5px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

p, h1, h2, h3, h4 {
  margin: 0 auto;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

.article-card {
  width: 280px;
  background-color: #ccc8c8d3;
  border-radius: 5px;
  margin: 10px;
  padding: 15px;
}

.article-description {
  margin-top: 0;
  margin-bottom: 10px;
}

.article-img {
  border-radius: 3px;
  justify-content: center;
  display: flex;
}

.form {
  padding: 10px;
}

.card {
  margin: 10px;
}

#newsWrapper {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #4056b4;
  border: none;
  border-radius: 2px;
  margin: 0 auto 20px;
  padding: 10px 20px;
  font-size: large;
  display: flex;
}

.load-more.hidden {
  display: none;
}

blockquote {
  color: #555;
  font-family: Dancing Script, cursive;
  font-size: 1.5rem;
}

hr {
  border: .5px dotted #bbb;
}

#main {
  width: 85%;
  height: 100%;
  background-color: #fff;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px #ccc;
}

#title {
  text-align: center;
  font-family: Pinyon Script, cursive;
  font-size: 4rem;
}

#img-div {
  text-align: center;
  margin: auto;
}

#img-div img {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 5px 10px #ccc;
}

#img-caption {
  margin-top: 5px;
  font-size: .9rem;
  font-style: italic;
}

#tribute-info {
  text-align: center;
  width: 75%;
  margin: auto;
}

#tribute-info ul {
  text-align: left;
}

#intro {
  margin: 30px auto;
  font-size: 1rem;
  font-style: italic;
  font-weight: bold;
}

#quote {
  background-image: url("https://www.damienvassart.com/test/fcc/responsive/img/qmarks.png");
  background-repeat: no-repeat;
  background-size: contain;
  margin: 70px auto 50px;
}

@media screen and (max-width: 440px) {
  #title {
    text-align: center;
    font-family: Pinyon Script, cursive;
    font-size: 2rem;
  }

  #img-caption {
    font-size: .6rem;
  }

  #intro {
    font-size: .8rem;
  }
}

@media screen and (min-width: 441px) and (max-width: 768px) {
  #title {
    text-align: center;
    font-family: Pinyon Script, cursive;
    font-size: 2.9rem;
  }

  #img-caption {
    font-size: .8rem;
  }

  #intro {
    font-size: .9rem;
  }
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #00000080;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 400px;
  height: 400px;
  background-color: #fff;
  border-radius: 10px;
  justify-content: center;
  align-items: flex-end;
  margin: auto;
  padding: 150px;
  display: flex;
}

.hide {
  display: none;
}

